@charset "IBM437";
@font-face {
    font-family: 'octicons';
    src: url("../fonts/octicons/octicons.eot?#iefix") format("embedded-opentype"), url("../fonts/octicons/octicons.woff") format("woff"), url("../fonts/octicons/octicons.ttf") format("truetype"), url("../fonts/octicons/octicons.svg#octicons") format("svg");
    font-weight: normal;
    font-style: normal;
}

/*

.octicon is optimized for 16px.
.mega-octicon is optimized for 32px but can be used larger.

*/
.octicon {
    font: normal normal 16px octicons;
    line-height: 1;
    display: inline-block;
    text-decoration: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.mega-octicon {
    font: normal normal 32px octicons;
    line-height: 1;
    display: inline-block;
    text-decoration: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

