    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    position: relative;
    -moz-border-radius: .3em;
    -webkit-border-radius: .3em;
    border-radius: .3em;
}

.zocial:before {
    content: "";
    border-right: 0.075em solid rgba(0, 0, 0, 0.1);
    float: left;
    font: 120%/1.65 zocial;
    font-style: normal;
