/*
The Miscellaneous Buttons
These button have no icons and can be general purpose buttons while ensuring consistent button style
Credit to @guillermovs for suggesting
*/
.zocial.primary,
.zocial.secondary {
    margin: 0.1em 0;
    padding: 0 1em;
}

.zocial.primary:before,
.zocial.secondary:before {
    display: none;
}

.zocial.primary {
    background-color: #333;
}

.zocial.secondary {
    background-color: #f0f0eb;
    color: #222;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.8);
}

/* Any browser-specific adjustments */
button:-moz-focus-inner {
    border: 0;
    padding: 0;
}

/* Reference icons from font-files
** Base 64-encoded version recommended to resolve cross-site font-loading issues
*/
@font-face {
    font-family: 'zocial';
    src: url('../fonts/zocial/zocial-regular-webfont.eot');
}

@font-face {
    font-family: 'zocial';
    src: url('../fonts/zocial/zocial-regular-webfont.ttf') format('truetype'), url('../fonts/zocial/zocial-regular-webfont.svg#zocialregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

/* ===============================================
  Circle Graphs
================================================= */
.circle-text {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
