/*==================================================
  Switches - Created with Pure CSS, no js required
==================================================== */
div.switch label {
    display: block;
    position: relative;
    color: transparent;
    background: #ddd;
    text-indent: 100%;
    width: 52px;
    height: 26px;
    cursor: pointer;
    transition: left 0.15s ease-out;
}

div.switch input {
    display: none;
}

div.switch input + label {
    margin-left: 0;
    margin-right: 0;
}

div.switch label:after {
    content: "";
    display: block;
    background: #fff;
    position: absolute;
    top: 3px;
    left: 3px;
    width: 19px;
    height: 19px;
    -webkit-transition: left 0.15s ease-out;
    -moz-transition: left 0.15s ease-out;
    transition: left 0.15s ease-out;
}

div.switch input:checked + label {
    background: #2ba6cb;
}

div.switch input:checked + label:after {
    left: 29px;
}

div.switch label {
    width: 52px;
    height: 26px;
}

div.switch label:after {
    width: 19px;
    height: 19px;
}

div.switch input:checked + label:after {
    left: 29px;
}

div.switch label {
    color: transparent;
    background: #dddddd;
}

div.switch label:after {
    background: #ffffff;
}

div.switch input:checked + label {
    background: #2ba6cb;
}

/* Switch Sizes */
div.switch.switch-lg label {
    width: 65px;
    height: 32px;
}

div.switch.switch-lg label:after {
    width: 26px;
    height: 26px;
}

div.switch.switch-lg input:checked + label:after {
    left: 35px;
}

div.switch.switch-sm label {
    width: 45px;
    height: 22px;
}

div.switch.switch-sm label:after {
    width: 16px;
    height: 16px;
}

div.switch.switch-sm input:checked + label:after {
    left: 26px;
}

div.switch.switch-xs label {
    width: 39px;
    height: 19px;
}

div.switch.switch-xs label:after {
    width: 13px;
    height: 13px;
}

div.switch.switch-xs input:checked + label:after {
    left: 22px;
}

div.switch.radius label {
    border-radius: 4px;
}

div.switch.radius label:after {
    border-radius: 3px;
}

/* Switch Option - Rounded */
div.switch.round {
    border-radius: 1000px;
}

div.switch.round label {
    border-radius: 26px;
}

div.switch.round label:after {
    border-radius: 26px;
}

/* Switch Option - Inline */
.switch.switch-inline {
    display: inline-block;
}

.switch.switch-inline + .switch.switch-inline {
    margin-left: 15px;
}

div.switch.switch-primary input:checked + label {
    background-color: #4a89dc;
}

div.switch.switch-success input:checked + label {
    background-color: #70ca63;
}

div.switch.switch-info input:checked + label {
    background-color: #3bafda;
}

div.switch.switch-warning input:checked + label {
    background-color: #f6bb42;
}

div.switch.switch-danger input:checked + label {
    background-color: #e9573f;
}

div.switch.switch-alert input:checked + label {
    background-color: #967adc;
}

div.switch.switch-system input:checked + label {
    background-color: #37bc9b;
}

div.switch.switch-dark input:checked + label {
    background-color: #3b3f4f;
}

/*==================================================
  Radios and Checkboxes
==================================================== */
.radio-custom,
.checkbox-custom {
    background-color: transparent;
    color: inherit;
}

/* hide default input */
.radio-custom input,
.checkbox-custom input {
    display: none;
}

.checkbox-custom label:empty,
.radio-custom label:empty {
    padding-left: 10px;
}

/* radio container */
.radio-custom label {
    cursor: pointer;
    position: relative;
    padding-left: 32px;
    margin-right: 15px;
    margin-bottom: 5px;
}

/* radio border */
.radio-custom label:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 64px;
    width: 20px;
    height: 20px;
    border: 2px solid #666;
}

/* check radio icon */
.radio-custom input[type=radio]:checked + label:after,
.radio-custom input[type=checkbox]:checked + label:after {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    border-radius: 64px;
    width: 10px;
    height: 10px;
    color: #666;
    background-color: #666;
}

/* alt style - square */
.radio-custom.square label:before {
    border-radius: 2px;
}

.radio-custom.square input[type=radio]:checked + label:after,
.radio-custom.square input[type=checkbox]:checked + label:after {
    border-radius: 2px;
}

/* checkbox container */
.checkbox-custom label {
    cursor: pointer;
    position: relative;
    padding-left: 32px;
    margin-right: 15px;
    margin-bottom: 5px;
}

/* checkbox border */
.checkbox-custom label:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 22px;
    height: 21px;
    background-color: #FFF;
    border: 1px solid #666;
    border-width: 2px;
    border-radius: 1px;
