/*===============================================
  B. Sidebar User Area
================================================= */
/* User Area Menu - A slide down user menu */
.user-menu {
    display: none;
    position: relative;
    left: 0;
    top: 0;
    height: 130px;
    width: 100%;
    padding: 10px;
    background-color: #282d33;
}

.user-menu a {
    position: relative;
    display: block;
    padding: 12px 5px 12px;
    margin-bottom: 3px;
    transition: all 0.2s ease;
}

.user-menu a:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

/* menu icon */
