/*===============================================
   B. Topbar Dropmenu
================================================= */
#topbar-dropmenu {
    z-index: 9999;
    overflow: hidden;
    display: none;
    position: relative;
    padding: 17px 20px 10px;
    height: auto;
    width: 100%;
    background: url("../img/patterns/topbar-bg.jpg") repeat -60px top;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.4) inset;
}

#topbar-dropmenu:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
}

.topbar-menu .metro-tile {
    opacity: 0;
    display: block;
    height: 95px;
    position: relative;
    padding: 15px 5px 0;
    margin-bottom: 8px;
    border-radius: 4px;
    text-align: center;
