    font-size: 44px;
}

.topbar-menu .metro-title {
    position: absolute;
    bottom: 0;
    left: 10px;
    font-size: 11px;
    font-weight: 600;
}

/* topbar menu modal */
.metro-modal {
    cursor: pointer;
    position: fixed;
    display: none;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 9998;
    background: rgba(0, 0, 0, 0.6);
}

